.container {
  max-width: 600px;
  margin: auto;
  padding: 20px;
  text-align: center;
}

.fileList {
  list-style: none;
  padding: 0;
}

.fileItem {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px;
  border-bottom: 1px solid #ddd;
}

.downloadBtn {
  text-decoration: none;
  background-color: #0070f3;
  color: white;
  padding: 8px 12px;
  border-radius: 5px;
}

.downloadBtn:hover {
  background-color: #0056b3;
}
